type serverMenu = {
	icon: string;
	name: string;
	url: string;
	txt: string;
}

let barcode="";


api.GetCurrentUserInfoAndMenus((data) => {


	Store.set('RoleName', data.RoleName);
	let curStage = data.stage[0].name.toLowerCase();
	Store.set('Stage', curStage);

    barcode=data.barcode;

    Store.set('barcode',barcode);

	if (curStage === 'wishlist') {
		let act = data.RoleName === 'exhibition' ? 'exhibition' : 'buyer';




		let wCounts = 0, checkFull = true;
		$.when(
			api[act + '_getList'](res => {
				wCounts = res.length;
			}),
			api[act + '_result'](res => {

				checkFull = (res.selected - res.max === 0);
			}),
		).then(function () {
			let isWishListBack : boolean|string|0 = request['s'] && request['s'] === 'wishlist';
			let sessionStore = window['sessionStorage'];
			if (!isWishListBack) {
				isWishListBack = sessionStore.getItem('iswishlistback') || 0;
			}
			else {
				sessionStore.setItem('iswishlistback', '1');
			}

			if (wCounts > 0 && !checkFull && !isWishListBack) {
				//location.href = menu.Wishlist.url;
				location.href = __uri('../wishlist/wishlist.html');
			}
			else {
				bindMainData(data);
			}
		});
	}
	else {
		bindMainData(data);
	}


    if(data.RoleName.toLowerCase() === 'buyer'){

		api.getBuyerByBarCode({barcode:barcode},(data) => {
            if(data[0].N=="Fully") {
                let lmMyTrip2 = document.getElementById('lmMyTrip2');
                lmMyTrip2.style.cssText += ';display:list-item';
                lmMyTrip2.addEventListener('tap', function () {
                	location.href = __uri('../operation/mytrip.html');
                });
                $("#lmMyTrip2").show();

			}

			if(data[0].N=="Fully" || data[0].N=="Partial") {
				if(curStage === 'pre-exhibiton' || curStage === 'exhibition'){

                    let lmMyScore2 = document.getElementById('lmMyScore2');
					lmMyScore2.style.cssText += ';display:list-item';
					lmMyScore2.addEventListener('tap', function () {
						location.href = __uri('../score/myScore.html');
					});

                    $("#lmMyScore2").show();
				}
				else {
                    let lmMyScore2 = document.getElementById('lmMyScore2');
					lmMyScore2.style.cssText += ';display:list-item';
					lmMyScore2.addEventListener('tap', function () {
						location.href = __uri('../score/description.html');
					});

                    $("#lmMyScore2").show();
				}
            }
		});
    }


});

function bindMainData(data) {

	if (data.RoleName === 'exhibition') {
		$('.onlyForExhibition').show();
		$('.notForExhibition').hide();
	}


    let userMenu = data.menus;

	(userMenu as serverMenu[]).forEach(function (v, i) {
        let m = menu[v.name];
		if (m) {
			v.icon = m.icon;
			v.url = m.url;
			v.txt = Languages.package['menu'][v.name];
			if (v.name === 'Wishlist') {
                let curStage = data.stage[0].name.toLowerCase();
				if (curStage === 'wishlist') {
					v.url = __uri('../wishlist/wishlist.html');
				}
			}
		}
		else {
			console.warn(v);
			v.icon = '';
			v.url = '';
			v.txt = v.name;
		}
	});


	document.getElementById('board').innerHTML = template('tpl-board', data);

	//document.getElementById('spUserName').innerText = Store.get('userName');
	document.getElementById('spUserName').innerText = `${data.company}`;
	document.getElementById('spRealName').innerText = `${data.firstName} ${data.lastName}`;
}




