var timesta = "";
var nonce = "";
var signa = "";

function initpage(){

    $.ajax({
        type: "POST",
        dataType: "html",
        async: false,
        url: "../../../../WechatAPI/QRcodeApi.aspx",
        data:
            {
                url: window.location.href.split('#')[0]
            },
        complete: function () { },
        error:function (XMLHttpRequest, textStatus, errorThrown)//出错处理
        {
            //alert("errorinfo of XMLHttpRequest:" + XMLHttpRequest + "|errorinfo of textStatus:" + textStatus + "|errorinfo of errorThrown:" + errorThrown);
        },
        success: function (data) {
            //alert(JSON.stringify(data));
            //var json = JSON.parseJSON(data);
			//alert(json);
            //eval("(" + data + ")"); //
            let jsons = `[` + data + `]`;

            var json = JSON.parse(jsons)[0];
            timesta = json.timestamp;
            nonce = json.nonceStr;
            signa = json.signature;
	    	//alert(json);
             //alert(json.url);
            // alert(timesta);
            // alert(nonce);
            // alert(signa);


			wx.config({
				debug: false,
				appId: 'wx701c4fa884a556e8',
				timestamp: timesta,
				nonceStr: nonce,
				signature: signa,
				jsApiList: [
					'scanQRCode'
				]
	    	});

            wx.error(function (res) {
                //alert(res.errMsg);
            });
        }
    });

};

initpage();


