let appscan=function() {
    if(Store.get('taskvalue')!="") {
        let param={};
        param.barcode=barcode;
        param.taskvalue=Store.get('taskvalue');
        api.appscan(param,data=>{
            if(data === 'ok') {
                let errorMsg = Languages.package['signSuccess'];
                mui.alert(`${errorMsg}`, '', Languages.package['okb']);
                Store.set('taskvalue','');
            }
            else if(data==='cannotsign') {
                let errorMsg = Languages.package['signNotOpen'];
                mui.alert(`${errorMsg}`, '', Languages.package['okb']);
			}
            else if(data==='repeat') {
                let errorMsg = Languages.package['noRepeat'];
                mui.alert(`${errorMsg}`, '', Languages.package['okb']);
            }
            else if(data==='badwalkin') {
                let errorMsg = Languages.package['badwalkin'];
                mui.alert(`${errorMsg}`, '', Languages.package['okb']);
            }
            else if(data==='okwalkin') {
                let errorMsg = Languages.package['okwalkin'];
                mui.alert(`${errorMsg}`, '', Languages.package['okb']);
            }
            else if(data==='nothing')
                ;
            else {
                let errorMsg = Languages.package['signError'];
                mui.alert(`${errorMsg}`, '', Languages.package['okb']);
			}
        });
    }
};

let userDevice = Store.get('userDevice');
if(userDevice==="app"){
    setInterval(appscan,1500);
}





